import React, { useEffect, useState } from "react";
import Header from "./Header.jsx";
import Footer from "./Footer.jsx";
import CheckoutForm from "./CheckoutForm.jsx";
import { Elements } from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";

const CheckOutPage = () => {
  const [billingInfo, setBillingInfo] = useState({
    full_name: "",
    email: "",
    phone: "",
    country: "",
    address: "",
    city: "",
    county: "",
    postal_code: "",
  });

  const [cartItems, setCartItems] = useState([]);
  const [user_id, setUser_Id] = useState(null);
  const [total, setTotal] = useState(null);
  const [paymentMethod, setPaymentMethod] = useState();
  const stripePromise = loadStripe(process.env.STRIPE_PUBLIC_KEY);

  // 1. Asigur-te c obii userId din momentul n care este disponibil
  useEffect(() => {
    fetch("https://api.devsite.cfd/auth/user", { credentials: "include" })
      .then((res) => res.json())
      .then((data) => {
        setUser_Id(data.user.id);
        console.log("Utilizatorul este:", data.user.id);
      })
      .catch((err) => console.error("Eroare la obtinerea utilizatorului", err));
  }, []);

  // 2. Obine produsele din co
  useEffect(() => {
    if (!user_id) return;
    fetch(`https://api.devsite.cfd/cart/data/${user_id}`, { credentials: "include" })
      .then((res) => {
        console.log("Rspuns server:", res); // Log pentru rspunsul HTTP
        return res.json();
      })
      .then((data) => {
        console.log("Datele din co:", data); // Log pentru datele din co
        setCartItems(data);
      })
      .catch((err) => console.error("Eroare la obtinerea produselor din cos", err));
  }, [user_id]);

  // 3. Obine totalul coului
  useEffect(() => {
    if (!user_id) return;
    fetch(`https://api.devsite.cfd/cart/total/${user_id}`, { credentials: "include" })
      .then((res) => res.json())
      .then((data) => {
        setTotal(data.total);
        console.log("Totalul este:", data.total);
      })
      .catch((err) => console.error("Eroare la obtinerea totalului", err));
  }, [user_id]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    if (name === "country") {
      setBillingInfo({ ...billingInfo, country: value, city: "" });
    } else {
      setBillingInfo({ ...billingInfo, [name]: value });
    }
  };

  const handleSubmit = (e) => {
    console.log("Aste e raspunsul e din handleSubmit" ,e);
    e.preventDefault();
    
    const { full_name, email, phone, country, address, city, county, postal_code } = billingInfo;

    if (!full_name || !email || !phone || !country || !address || !city || !county || !postal_code) {
      alert("Toate cmpurile sunt obligatorii!"); // Afieaz alert dac exist cmpuri lips
      return; // Oprete execuia funciei i nu trimite formularul
    }
   
    console.log("Datele de facturare:", billingInfo); // Log pentru datele de facturare

    fetch(`https://api.devsite.cfd/billing_info/${user_id}`, {
      credentials: "include",
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(billingInfo),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log("Datele primite in billing_info(front-end):", data);
        setBillingInfo(data);
      })
      .catch((err) => console.error("Eroare la trimiterea datelor de facturare", err));
  };

  // Functia de trimitere comenzii
  const orders = () => {
    return fetch(`https://api.devsite.cfd/orders/${user_id}`, {
      credentials: "include",
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ total_price: total, payment_method: paymentMethod, billing_id: billingInfo.id }),
    })
      .then((res) => res.json())
      .then((result) => {
        console.log("Datele primite:", result);
        if (!result || result.length === 0) {
          console.error(" Eroare: rspunsul API nu conine date!");
          return null;
        }
        console.log("order_id:", result[0].id); // Accesm primul obiect din array
        const order_id = result[0].id;
        return order_id;
      })
      .catch((err) => {
        console.error("Eroare la trimiterea comenzii", err);
        return null; // returneaz null n caz de eroare
      });
  };

  // Functia de trimitere produse in comanda
  const order_items = (order_id) => {
    cartItems.forEach((item) => {
      console.log("Produs trimis:", item); // Verific ce date sunt trimise
      fetch("https://api.devsite.cfd/order_items", {
        credentials: "include",
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          product_id: item.product_id,
          quantity: item.quantity,
          price: item.product_price,
          order_id: order_id,
        }),
      })
        .then((res) => res.json())
        .then((data) => {
          console.log(" Produs adugat n order_items:", data);
        })
        .catch((err) => console.error(" Eroare la trimiterea produsului:", err));
    });
  };

  // Procesul complet al comenzii
  const handleOrderProcess = (e) => {
    console.log("Asta e raspunsul din handleOrderProcess", e);
    e.preventDefault();
    orders().then((order_id) => {
      if (order_id) {
        console.log("order_id primit:", order_id); // Log pentru verificare
        order_items(order_id); // Folosete newOrderId aici
      } else {
        console.error("Eroare: newOrderId nu a fost primit!");
      }
    });
  };

  const citiesByCountry = {
    Romania: [
      "Bucureti",
      "Cluj-Napoca",
      "Timioara",
      "Iai",
      "Constana",
      "Braov",
      "Craiova",
      "Galai",
      "Ploieti",
      "Oradea",
      "Bacu",
      "Arad",
      "Sibiu",
      "Bistria",
      "Brila",
      "Baia Mare",
      "Rmnicu Vlcea",
      "Suceava",
      "Trgu Mure",
      "Focani",
    ],
    Franta: [
      "Paris",
      "Marseille",
      "Lyon",
      "Toulouse",
      "Nice",
      "Nantes",
      "Strasbourg",
      "Montpellier",
      "Bordeaux",
      "Lille",
      "Rennes",
      "Reims",
      "Le Havre",
      "Saint-tienne",
      "Toulon",
      "Le Mans",
      "Aix-en-Provence",
      "Clermont-Ferrand",
      "Brest",
      "Limoges",
    ],
    Belgia: [
      "Bruxelles",
      "Antwerpen",
      "Ghent",
      "Charleroi",
      "Lige",
      "Brugge",
      "Namur",
      "Leuven",
      "Mons",
      "La Louvire",
      "Kortrijk",
      "Hasselt",
      "Mechelen",
      "Sint-Niklaas",
      "Oostende",
      "Aalst",
      "Seraing",
      "Tournai",
      "Genk",
      "Beringen",
    ],
    Suedia: [
      "Stockholm",
      "Gothenburg",
      "Malm",
      "Uppsala",
      "Vsters",
      "rebro",
      "Linkping",
      "Helsingborg",
      "Jnkping",
      "Norrkping",
      "Lund",
      "Ume",
      "Bors",
      "Sundsvall",
      "Karlstad",
      "Trollhttan",
      "Eskilstuna",
      "Gvle",
      "Vxj",
      "Kalmar",
    ],
    Polonia: [
      "Warszawa",
      "Krakw",
      "d",
      "Wrocaw",
      "Pozna",
      "Gdask",
      "Szczecin",
      "Bydgoszcz",
      "Lublin",
      "Biaystok",
      "Katowice",
      "Gdynia",
      "Czstochowa",
      "Radom",
      "Sosnowiec",
      "Toru",
      "Kielce",
      "Rzeszw",
      "Gliwice",
      "Zabrze",
    ],
  };

  const countiesByCountry = {
    Romania: [
      "Alba",
      "Arad",
      "Arge",
      "Bacu",
      "Bihor",
      "Bistria-Nsud",
      "Botoani",
      "Brila",
      "Braov",
      "Bucureti",
      "Buzu",
      "Clrai",
      "Cara-Severin",
      "Cluj",
      "Constana",
      "Covasna",
      "Dmbovia",
      "Dolj",
      "Galai",
      "Giurgiu",
      "Gorj",
      "Harghita",
      "Hunedoara",
      "Ialomia",
      "Iai",
      "Ilfov",
      "Maramure",
      "Mehedini",
      "Mure",
      "Neam",
      "Olt",
      "Prahova",
      "Satu Mare",
      "Slaj",
      "Sibiu",
      "Suceava",
      "Teleorman",
      "Timi",
      "Tulcea",
      "Vaslui",
      "Vlcea",
      "Vrancea",
    ],
    Franta: [
      "Ain",
      "Aisne",
      "Allier",
      "Alpes-de-Haute-Provence",
      "Hautes-Alpes",
      "Alpes-Maritimes",
      "Ardche",
      "Ardennes",
      "Arige",
      "Aube",
      "Aude",
      "Aveyron",
      "Bouches-du-Rhne",
      "Calvados",
      "Cantal",
      "Charente",
      "Charente-Maritime",
      "Cher",
      "Corrze",
      "Cte-d'Or",
      "Ctes-d'Armor",
      "Creuse",
      "Dordogne",
      "Doubs",
      "Drme",
      "Eure",
      "Eure-et-Loir",
      "Finistre",
      "Gard",
      "Haute-Garonne",
      "Gers",
      "Gironde",
      "Hrault",
      "Ille-et-Vilaine",
    ],
    Belgia: [
      "Bruxelles-Capital",
      "Antwerpen",
      "Flandra de Est",
      "Hainaut",
      "Lige",
      "Flandra de Vest",
      "Namur",
      "Flandra de Brabant",
      "Hainaut",
      "Flandra de Vest",
      "Limburg",
    ],
    Suedia: [
      "Stockholm",
      "Vstra Gtaland",
      "Skne",
      "Uppsala",
      "Vstmanland",
      "rebro",
      "stergtland",
      "Helsingborg",
      "Jnkping",
      "Norrkping",
      "Lund",
      "Vsterbotten",
      "Kronoberg",
      "Kalmar",
    ],
    Polonia: [
      "Dolnolskie",
      "Kujawsko-pomorskie",
      "Lubelskie",
      "Lubuskie",
      "dzkie",
      "Maopolskie",
      "Mazowieckie",
      "Opolskie",
      "Podkarpackie",
      "Podlaskie",
      "Pomorskie",
      "lskie",
      "witokrzyskie",
      "Warmisko-mazurskie",
      "Wielkopolskie",
      "Zachodniopomorskie",
    ],
  };

  return (
    <div className="CheckOutPage-div">
      <Header />
      <main>
        <div className="billingData-form-div">
          <form onSubmit={handleSubmit}>
            <input
              type="text"
              id="full_name"
              name="full_name"
              placeholder="Nume complet"
              onChange={handleChange}
            />
            <br />
            <input
              type="email"
              id="email"
              name="email"
              placeholder="Email"
              onChange={handleChange}
            />
            <br />
            <input
              type="tel"
              id="phone"
              name="phone"
              placeholder="Telefon"
              onChange={handleChange}
            />
            <br />
            <select id="country" value={billingInfo.country} name="country" onChange={handleChange}>
              <option  value="">Selecteaza ara</option>
              {Object.keys(citiesByCountry).map((country) => (
                <option  key={country} value={country}>
                  {country}
                </option>
              ))}
            </select>
            <br />
            <input
              type="text"
              id="address"
              name="address"
              placeholder="Adresa"
              onChange={handleChange}
            />
            <br />
            <select id="city" value={billingInfo.city} name="city" onChange={handleChange}>
              <option value="">Selecteaza orasul/localitatea</option>
              {billingInfo.country &&
                citiesByCountry[billingInfo.country].map((city) => (
                  <option key={city} value={city}>
                    {city}
                  </option>
                ))}
            </select>
            <br />
            <select id="county" value={billingInfo.county} name="county" onChange={handleChange}>
              <option value="">Selecteaza judetul</option>
              {billingInfo.country &&
                countiesByCountry[billingInfo.country].map((county) => (
                  <option key={county} value={county}>
                    {county}
                  </option>
                ))}
            </select>
            <br />
            <input
              type="text"
              id="postal_code"
              name="postal_code"
              placeholder="Cod potal"
              onChange={handleChange}
            />
            <br />
          </form>
        </div>
        <div className="checkOutForm-div">
          <Elements stripe={stripePromise}>
            <CheckoutForm 
              paymentMethod={paymentMethod}
              setPaymentMethod={setPaymentMethod}
              amount={total}
              handleBillingInfoSubmit={handleSubmit}
              handleOrderProcess={handleOrderProcess}
            />
          </Elements>
        </div>
      </main>
      <Footer />
    </div>
  );
};

export default CheckOutPage;